// Variables

$bodyFont:'Poppins', sans-serif;
$headingFont: 'Oswald', sans-serif;

$lightGrey: #a8b0b2;
$darkGrey:#545454;
$darkPurple:#161a35;
$lightPurple:#820BBB;
$siteWhite:#ffffff;
$textBlack:#2b2e2f;
$pink: #e456a9;

/*
====================================
Default Styling
====================================
*/

img {
	max-width: 100%;
}

body {
	color: #333;
	font-size: 10px;
}

.container {
	max-width: 90%;
	margin: 0 auto;
}

.smallContainer {
	width: 65%;
	max-width:850px;
	margin: 0 auto;
	transition:all 0.3s ease;
}

section{
	padding-top: 70px;
}

// Social bar in header and footer 

.socialHeaderContainer, .socialFooterContainer {
	display:flex;
	i{
		font-size:1.6rem;
		transition:1s all ease-out;
		color:$lightGrey;
	}
	a {
		padding:7px 9px;
		margin:8px;
		display: flex;
		justify-content:center;
		align-items:center;
		position: relative;
		&:after {
			background:linear-gradient(-30deg, #e456a9 5%, #aa61cc 95%);
			content:"";
			opacity:0;
			width:inherit;
			height:inherit;
			position: absolute;
			z-index:-10;
			top:0;
			left:0;
			right:0;
			bottom:0;
			border-radius:50%;
			transition:opacity 1s ease-out;
		}
		&:hover:after , &:focus:after{
			opacity:1;
		}
		&:hover, &:focus {
			color:white;
		}
		&:hover i{
			color:white;
		}
	}
}

.logoContainer {
	height:100px;
	width:100px;
	z-index:10;
}

.logoContainer:hover svg, .logoContainer:hover svg {
	transform:scale(1.2);
	stroke-width:8px;
}

svg.logo {
	fill:transparent;
	stroke-width:12px;
	stroke:white;
	position: relative;
	stroke-dashoffset:1050;
	stroke-dasharray:1050;
	animation:3s dash forwards ease-in-out
	;
	transition:0.3s all ease;
}

// js Classes
.jsShow {
	display: block!important;
}
.jsFixed {
	color:black;
}

.jsColorShow {
	filter: none!important;
	filter: grayscale(0%);
}

// Imports

@import 'normalize';
@import 'typography';
@import 'navigation';
@import 'header';
@import 'aboutAndSkills';
@import 'portfolio';
@import 'contact';
@import 'footer';
@import 'breakpoints';





